











import {Component, Vue, Prop} from 'vue-property-decorator';

@Component
export default class UserIcon extends Vue {

  @Prop()
  user!: string;
  @Prop()
  role!: string;
  @Prop()
  image!: string;

  get src() {
    // eslint-disable-next-line @typescript-eslint/no-var-requires
    const src = require(`@/assets/${this.image}`);
    return `url("${src}")`;
  }


}
