



















import {Component, Prop, Vue} from 'vue-property-decorator';

@Component
export default class ActionButton extends Vue {
  @Prop()
  type!: 'add' | 'remove';

  get buttonClassName() {
    return `type-${this.type} button`;
  }

  get isAdd() {
    return this.type == 'add';
  }

  get isRemove() {
    return this.type == 'remove';
  }
}
